.App {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  overflow: hidden;
}

.RoundContainer {
  padding: 30px 10px;
}

.PrevRoundContainer {
  text-align: right;
}

.RoundButton {
  background-color: white !important;
}

.ButtonIcon {
  color: #175088;
  font-weight: bold;
}

.NextPrevRoundContainer {
  text-align: left;
}

.PrevRoundArrowIcon {
  transform: rotate(180deg);
}

.RoundLabel {
  text-align: center;
  color: white;
  font-weight: bold !important;
}

.TimerContainer {
  text-align: center;
  position: relative;
  display: block;
}

.TimerProgress {
  transform: rotate(-90) !important;
}

.MuiCircularProgress-colorPrimary {
  color: #fce205 !important;
}

.MuiCircularProgress-root {
  width: 250px !important;
  height: 250px !important;
}

.TimerLabel {
  color: white;
  font-weight: bold !important;
  position: absolute;
  text-align: center;
  left: 0;
  right: 0;
  top: 100px;
  font-size: 70px !important;
}

.TimerIcon {
  color: white;
  font-weight: bold !important;
  position: absolute;
  text-align: center;
  left: 0;
  right: 0;
  top: 20px;
}

.TimerActionsContainer {
  text-align: center;
}

.TimeButton {
  margin: 10px !important;
  background-color: white !important;
}

.JokerButton {
  padding-left: 25px !important;
  padding-right: 25px !important;
  font-weight: bold !important;
  font-size: 25px !important;
  color: #fce205 !important;
  background-color: #3d5586 !important;
  border-color: #fce205 !important;
  -webkit-box-shadow: 0px 2px 10px 2px rgba(201, 235, 255, 0.62) !important;
  -moz-box-shadow: 0px 2px 10px 2px rgba(201, 235, 255, 0.62) !important;
  box-shadow: 0px 2px 10px 2px rgba(201, 235, 255, 0.62) !important;
  padding-top: 5px !important;
  padding-bottom: 5px !important;
  border-radius: 15px !important;
  display: block;
  position: relative;
  overflow: hidden;
}

.JokerTop {
  position: absolute;
  background-color: #dac244;
  width: 100%;
  height: 7px;
  top: 0;
}

.JokerBottom {
  position: absolute;
  background-color: #dac244;
  width: 100%;
  height: 7px;
  bottom: 0;
}

.JokerButton.MuiFab-root {
  line-height: 1 !important;
}

.BonusRoundContainer {
  margin-top: 25px !important;
  text-align: center;
}

.BonusRoundButton {
  padding-left: 40px !important;
  padding-right: 40px !important;
  font-weight: bold !important;
  font-size: 22px !important;
  color: white !important;
  background-color: #cc1c75 !important;
  -webkit-box-shadow: 0px 2px 10px 2px rgba(201, 235, 255, 0.62) !important;
  -moz-box-shadow: 0px 2px 10px 2px rgba(201, 235, 255, 0.62) !important;
  box-shadow: 0px 2px 10px 2px rgba(201, 235, 255, 0.62) !important;
}

#alert-dialog-title h2 {
  font-size: 22x !important;
  font-weight: bold !important;
  text-align: center;
}

.JokersWinned {
  padding-left: 20px !important;
  padding-right: 20px !important;
  font-weight: bold !important;
  font-size: 16px !important;
  color: #fce205 !important;
  background-color: #3d5586 !important;
  border-color: #fce205 !important;
  border: 3px solid !important;
}

.JokersWinnedCounter {
  font-size: 26px;
  padding-right: 10px;
}

.BonusCorrectAnswers {
  padding-left: 20px !important;
  padding-right: 20px !important;
  font-weight: bold !important;
  font-size: 26px !important;
  padding-top: 22px !important;
  padding-bottom: 22px !important;
  background-color: #00b300 !important;
  border-color: #00b300 !important;
  border: 3px solid !important;
  color: white !important;
  border-radius: 38px !important;
  margin-right: 10px;
}

.BonusCorrectAnswersChip {
  color: white !important;
  font-size: 35px !important;
}

.BonusRoundLabels {
  text-align: center;
}

.BonusRoundTimerLabel {
  color: black;
  font-weight: bold !important;
  position: absolute;
  text-align: center;
  left: 0;
  right: 0;
  top: 120px;
}

.BonusTimerContainer {
  text-align: center;
  position: relative;
  display: block;
}

.BonusTimerContainer .MuiCircularProgress-colorPrimary {
  color: #ef17f1 !important;
}

.BonusTimerActionsContainer {
  text-align: center;
}

.BonusTimerCorrectButton {
  background-color: #00b300 !important;
  color: white !important;
  font-size: 15px !important;
  font-weight: bold !important;
}

.BonusTimerCorrectButtonIcon {
  padding-right: 5px;
  font-size: 35px !important;
}

.Background {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -1;
}
